// Variables

// Mixins
@mixin hover-supported {
    @media not all and (pointer: coarse) {
        &:hover {
            @content;
        }
    }
}

// Animations
@keyframes waiting {
    0% {
        transform: scale(1);
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.18),
            0 8px 16px rgba(0, 0, 0, 0.36),
            0 0 0 rgba(0, 0, 0, 0),
            0 0 0 0 rgba(255, 255, 255, 0);
    }

    25% {
        transform: scale(0.6);
        box-shadow: 0 0 0 rgba(0, 0, 0, 0),
            0 0 0 rgba(0, 0, 0, 0),
            0 0 0 rgba(0, 0, 0, 0),
            0 0 0 0 rgba(255, 255, 255, 0);
    }

    50% {
        transform: scale(1.2);
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.14),
            0 3px 31px -2px rgba(0, 0, 0, 0.2),
            0 1px 5px 0 rgba(0, 0, 0, 0.12),
            0 0 0 15vw rgba(255, 255, 255, 1);
    }

    100% {
        transform: scale(1);
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.18),
            0 8px 16px rgba(0, 0, 0, 0.36),
            0 1px 5px 0 rgba(0, 0, 0, 0.12),
            0 0 0 100vw rgba(255, 255, 255, 0);
    }
}

@keyframes loaded {
    0% {
        transform: scale(1);
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.18),
            0 8px 16px rgba(0, 0, 0, 0.36),
            0 0 0 rgba(0, 0, 0, 0),
            0 0 0 0 rgba(255, 255, 255, 0);
    }

    100% {
        opacity: 0;
        transform: scale(0);
        box-shadow: 0 0 0 rgba(0, 0, 0, 0),
            0 0 0 rgba(0, 0, 0, 0),
            0 0 0 0 rgba(0, 0, 0, 0),
            0 0 0 0 rgba(255, 255, 255, 0);
    }
}

// Styles
* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    box-sizing: border-box;
}

.noselect {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

:focus {
    outline: 0;
}

html,
body {
    height: 100%;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f2f2f2;
    font-family: Roboto, Helvetica, Arial, sans-serif;
}

.flex-center--all {
    display: flex;
    align-items: center;
    justify-content: center;
}

button.firebaseui-idp-google {
    padding: 12px 20px;
    background: white;
    border: 0;
    border-radius: 99rem;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
        0 1px 5px 0 rgba(0, 0, 0, 0.12);
    outline: 0;
    overflow: hidden;
    will-change: box-shadow;
    transform: scale(1);
    transition: 0.2s all;
    cursor: pointer;

    @include hover-supported() {
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.18), 0 8px 16px rgba(0, 0, 0, 0.36);
    }

    &.clicked {
        padding: 12px;
        background: #f6f6f6;
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.18), 0 8px 16px rgba(0, 0, 0, 0.36);
        filter: grayscale(100%);
        cursor: default;

        .firebaseui-idp-text {
            height: 0;
            width: 0;
            margin: 0;
            opacity: 0;
        }
    }

    &.waiting {
        animation: waiting 2s linear infinite
    }

    &.loaded {
        animation: loaded 0.2s linear forwards
    }

    .firebaseui-idp-icon-wrapper {
        height: 18px;
        width: 18px;

        img {
            height: 100%;
            width: auto;
        }
    }

    .firebaseui-idp-text {
        margin-left: 0.5rem;
        font-weight: 500;
        transition: 0.3s height width;
    }

    .firebaseui-idp-text-long {
        display: none;

        @media (min-width: 269px) {
            display: inline-block;
        }
    }

    .firebaseui-idp-text-short {
        display: inline-block;

        @media (min-width: 269px) {
            display: none;
        }
    }
}